.user {
  display: flex;
  flex-wrap: wrap;
  margin: 1.4em;
}

.userAvatar img {
  border-radius: 50%;
  width: 80px;
}

.rightContent {
  background: #f7f7f7;
  border-radius: 0.4em;
  flex: 1;
  padding: 0.8em;
  margin-left: 0.6em;
}

.leftContent {
  text-align: center;
  min-width: 8.4em;
}
